@import url(https://fonts.googleapis.com/css2?family=Yantramanav&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App dev {
  background-color: #efefef;
  background-repeat: no-repeat;
}

.overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-size: 50% 50%;
  opacity: 0.5;
  z-index: -1;
}

.title .header {
  font-size: 32px;
}

.title .subtitle {
  background: #000000;
  padding: 2rem;
  color: #ffffff;
  text-align: center;
  font-weight: bolder;
  font-size: 20px;
  text-transform: uppercase;
  margin: 1rem 1rem;
}

hr.impact {
  width: 40%;
  border-top: 3px dashed coral;
  background: transparent;
  box-shadow: 0 0 10px 0 #dda288;
  /* background-image: linear-gradient(to right, #ccc, #333, #ccc); */
}

.title .description {
  color: #333;
  font-size: 16px;
  font-family: "Yantramanav", sans-serif;
  font-weight: bold;
}

.title .desc-list {
  color: orangered;
  font-size: 32px;
  font-family: "Yantramanav", sans-serif;
  font-weight: bolder;
}

.desc-list.sm {
  opacity: 0.8;
  font-size: 24px;
}

.desc-list.md {
  opacity: 0.6;
  font-size: 28px;
}

.desc-list.lg {
  color: red;
  font-size: 36px;
}

.pop {
  font-size: larger;
}

p {
  text-indent: 1rem;
}

